<template>
  <div class="project page-container d-flex">
    <page-header></page-header>
    <div class="info-wrap pr-8">
      <h1 class="mt-0 project-name-mobile">{{ currentPorject.name }}</h1>
      <h3 class="mb-2 text-primary">Company/Organize</h3>
      <p class="mb-8 break-word">{{ currentPorject.company }}</p>
      <h3 class="mb-2 text-primary">Role</h3>
      <p class="mb-8 break-word">{{ currentPorject.role }}</p>
      <h3 class="mb-2 text-primary">Year</h3>
      <p class="mb-8">{{ currentPorject.year }}</p>
      <h3 class="mb-2 text-primary">My Duties</h3>
      <p class="mb-8 break-word">{{ currentPorject.duties }}</p>
      <h3 class="mb-2 text-primary">Tech Stack</h3>
      <p class="mb-8 break-word">
        <span
          v-for="(v, i) in currentPorject.stack"
          :key="i"
          class="keyword fs-0"
          :style="{ backgroundColor: getColor(v) }"
        >
          {{ v }}
        </span>
      </p>
      <template v-if="currentPorject.link">
        <h3 class="mb-2 text-primary">Link</h3>
        <a class="text-white" :href="currentPorject.link" target="_blank">{{
          currentPorject.link
        }}</a><br/>
        <a v-if="currentPorject.link2" class="text-white mt-2 d-block" :href="currentPorject.link2" target="_blank">{{
          currentPorject.link2
        }}</a>
      </template>
    </div>
    <div class="flex-1">
      <h1 class="mt-0 project-name-pc">{{ currentPorject.name }}</h1>
      <template
        v-if="!!currentPorject.paragraphs && currentPorject.images.length > 0"
      >
        <h2 class="mb-3 text-primary">Gallery</h2>
        <PicViewer
          :key="index + 'i'"
          :viewerjsProps="VIEWER_PROPS"
          :value="getImgList(currentPorject.images)"
        ></PicViewer>
      </template>
      <template
        v-if="
          !!currentPorject.paragraphs && currentPorject.paragraphs.length > 0
        "
      >
        <div
          v-for="(v, i) in currentPorject.paragraphs"
          :key="i"
          class="info-paragraph"
        >
          <h2 class="mb-0 text-primary">{{ v.title }}</h2>
          <template v-for="(item, index) in v.content">
            <img
              :key="index + 'i'"
              v-if="item.type && item.type == 'img'"
              :class="item.class"
              class="w-100 info-img"
              :src="getImg(item.url)"
            />
            <p class="info-text mt-2" :key="index + 'p'" v-else>{{ item }}</p>
          </template>
        </div>
      </template>
    </div>

    <!--  -->
  </div>
</template>
      
<script>
import { PROJECT_DETAILS } from "@/constant";
import PageHeader from "@/components/page-header.vue";
import PicViewer from "pic-viewer";
export default {
  name: "ProjectDetail",
  components: { PageHeader, PicViewer },
  data() {
    return {
      PROJECT_DETAILS,
      projectName: "",
      currentPorject: {},
      VIEWER_PROPS: { zoomRatio: 1 },
    };
  },
  computed: {
    getColor() {
      return function (keyword) {
        if (keyword == "Java") {
          return "#ef8d07";
        } else if (keyword == "Spring") {
          return "#6db33f";
        } else if (keyword == "Vue") {
          return "#42b983";
        } else if (keyword == "ElementUI") {
          return "#409eff";
        } else if (keyword == "PostgreSQL") {
          return "#336791";
        } else if (keyword == "PHP") {
          return "#7a86b8";
        } else if (keyword == "Bootstrap") {
          return "#712cf9";
        } else if (keyword == "MySQL") {
          return "#3E6E93";
        } else if (keyword == "HTML") {
          return "#e54c21";
        } else if (keyword == "Unity") {
          return "#000000";
        } else if (keyword == "C++") {
          return "#048";
        } else if(keyword == 'git') {
          return "#f54d27";
        }else {
          return "#1890ff";
        }
      };
    },
    getImg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
    getImgList() {
      return function (urls) {
        return urls.map((v) => require(`@/assets/${v}`));
      };
    },
  },
  methods: {},
  mounted() {
    const {
      params: { projectName },
    } = this.$route;
    this.projectName = projectName;
    this.currentPorject = { ...this.PROJECT_DETAILS[projectName] };
  },
};
</script>
      
<style scoped lang="scss">
@import "../style/project.scss";
</style>
      